/*#region pagr*/
.page {
  font-family: "Cinzel", serif;
  font-style: 16px;
  line-height: 27px;
  color: #161616;

  scroll-behavior: smooth;
}
/*
.cinzel-uniquifier {
  
  font-optical-sizing: auto;
  font-weight: 400 700;
  font-style: normal;
}

.roboto-regular {
  font-family: "Roboto", serif;
  font-weight: 400;
  font-style: normal;
}
*/
.page__body {
  margin: 0;
}

.page__menu {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;

  opacity: 0;
  pointer-events: none;
}

.page__menu:target {
  opacity: 1;
  pointer-events: all;
}
/*#endregion*/

/*#region header*/
.header {
  box-sizing: border-box;
  height: 100vh;
  background-image: url(../images/header-bg-mobile.png);
  background-size: cover;
  background-position: center;
    
  display: flex;
  flex-direction: column;
  
}

.header__top {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header__icons {
  padding-top: 0%px;
  display: flex;
  gap: 24px;
}

.header__title {
  width: 280px;
  height: 80px;
  margin-top: 200px;
  font-style: Cinzel;
  font-weight: 700;
  font-size: 40px;
  line-height: 40px;
  letter-spacing: -1.5px;
  align-items: center;
  color: #FFFFFF;
}

.header__bottom {
  padding: 0px 20px 288px;
}


/*#endregion*/

/*#region menu*/
.top-bar {
  padding: 25px 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.menu {
  box-sizing: border-box;
  
  height: 100vh;
  background-color: #D12D35;
  font-weight: 700;
  text-transform: uppercase;
}

.menu__nav {
  margin-bottom: 32px;
}

.menu__content {
  padding-inline-start: 20px;
  position: relative;
}


.menu__fifth-avenue {
  display: block;
  margin-bottom: 8px;
  font-size: 16px;
  line-height: 27px;
  color: #FFFFFF;
  text-decoration: none;
}

.menu__cloisters {
  display: block;
  margin-bottom: 16px;
  font-size: 16px;
  line-height: 27px;
  color: #FFFFFF;
  text-decoration: none;
}

.menu__call-to-order {
  display: block;
  width: fit-content;
  padding-bottom: 7px;
  border-bottom: 1px solid #FFFFFF;
  font-size: 12px;
  line-height: 16px;
  letter-spacing: 2px;
  color: #FFFFFF;
  text-decoration: none;
}
/*#endregion*/

/*#region nav*/
.nav__link {
  color: #FFFFFF;
  text-decoration: none;
  font-size: 22px;
  line-height: 22px;
  letter-spacing: 2px;
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-direction: column;
  gap: 32px;
}
/*#endregion*/

/*#region icon*/
.icon {
  display: block;
  height: 24px;
  width: 24px;
}

.icon--phone {
  background-image: url(../images/icons/icon-phone.png);
}

.icon--menu {
  background-image: url(../images/icons/icon-burger-menu.png);
}

.icon--close {
  background-image: url(../images/icons/icon-close.png);
}
/*#endregion*/

